import AccountDetails from "./accountDetails-model.js";
const accountDetails = new AccountDetails()
export default {
	data() {
		return {
			userInfo: {}
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			// 使用vuex获取原有的用户信息
			that.userInfo = that.$store.state.userInfo;
			that.getListData(() => {
				callBack && callBack();
			})
		},
		getListData() {
			const that = this
			accountDetails.getListData({}, (res) => {
				if (res.status_code == 'ok') {
					let userInfo = that.$store.state.userInfo;
					that.userInfo = Object.assign(userInfo, res.data)
					that.$store.commit('updateUserInfo', that.userInfo);
				}
				callBack && callBack();
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return accountDetails.onShareAppMessage(shareData);
	}
}
